/* Site Header
---------------------------------------------------------------------------------------------------- */

.site-header {
	/*background-color: #fff;*/
	box-shadow: 0 0 20px rgba(0,0,0,0.05);
	padding: 0 30px;
        background-image:url('img/full-width-grass-short.png');
        background-position:bottom center;
        background-repeat:repeat-x;
        
        .wrap {
            max-width:1200px;
            margin: 0px auto;
            padding-top:30px;
        }
        
        .header-widget-area {
            display:flex;
            flex-flow:row-reverse;
            margin-bottom:-50px;
        }
}

/* Title Area
--------------------------------------------- */

.title-area {
	float: left;
	padding-bottom: 25px;
	padding-top: 25px;
}

.wp-custom-logo .title-area {
	max-width: 350px;
	padding-bottom: 5px;
	padding-top: 5px;
}

.site-title {
	font-size: 20px;
	font-size: 2rem;
	font-weight: 600;
	line-height: 1;
	margin-bottom: 0;
}

.site-title a,
.site-title a:focus,
.site-title a:hover {
	color: #333;
	text-decoration: none;
}

.site-description,
.wp-custom-logo .site-title {
	border: 0;
	clip: rect(0, 0, 0, 0);
	height: 1px;
	overflow: hidden;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
}
@media only screen and (min-width: 960px) {
    
    /* Site Header
	--------------------------------------------- */

	.site-header {
		width: 100%;
		z-index: 9999;
	}
        
}